@import url(https://fonts.googleapis.com/css?family=Lato:400,900,900i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    color: black;
}

a:hover {
    color: black
}
.navbar {
    margin-bottom: 20px;
}

.navbar a {
    color: black;
}

.nav-item {
    display: flex;
}
.figure-img {
    margin: 20px;
    -webkit-transition: 0.5s ease;
    transition: 0.5s ease;

}

.figure-img:hover {
    border: 15px solid #35363a;
    cursor: pointer;
}
   
  .box {
    display: inline-block;       
    border: 1px solid black;
    width: 15px;
    height: 15px;
    margin-left: -1px;
    margin-bottom: -1px;
    }
  
  .off {
    background-color: lightgray;
  }
  
  .on {
    background-color: green;
  }
    
  .box:hover { 
    background: #00CCFF;
  }
  
  .center {
    margin: auto;
    display: table;
    margin-top: 10px;
  }
  
  .grid {
    width: 150px;
    line-height: 0;
    margin: auto;
    box-shadow: 0px 0px 20px white;
    margin-top: 20px;
  }
    
  h1, h2, h3, h4 {
    color:#292a2d;
    margin: auto;
    text-align: center;
    width: 50%;
    margin-top: 10px;
  }
  
  h4 {
    margin-top: 0px;
  }
.jumbotron {
    margin-top: 50px;
    background-color: #d1d4d7;
    text-align: center;
    color: #292a2d;
}

.project-detail {
    padding-top: 20px ;
    padding-bottom: 20px ;
}
*{
    font-family: 'Lato';
    font-weight: 600;
}


